import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import get from 'lodash/get';
import FiberManualRecordOutlinedIcon from '@material-ui/icons/FiberManualRecordOutlined';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import Loadable from '@loadable/component';
import Style from './style';
import { ProgrammesContent } from '../../constants/resources';

const Tile = Loadable(() => import('../Tile'));

export interface Props extends WithStyles<typeof Style> {
	image: string;
	contentUp: string;
	contentDown: string;
	leftContent?: boolean;
}
/**
 * ImageInfoBlocks for founders section.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class ImageInfoBlocks extends React.PureComponent<Props> {
	componentDidMount() {}

	getContent = (item) => {
		const { classes } = this.props;
		return (
			<div>
				<div className={classes.blockTitle}>
					<img src={item.icon} alt="icon" className={clsx(classes.icon)} />
					<span className={classes.marginLeft}>{item.title}</span>
				</div>
				<div className={classes.content}>{item.content}</div>
				<div className={classes.info}>{item.info}</div>
			</div>
		);
	};

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes } = this.props;
		const image = get(this.props, 'image', '');
		const name = get(this.props, 'name', '');
		const contentUp = get(this.props, 'contentUp', '');
		const contentDown = get(this.props, 'contentDown', '');
		const leftContent = get(this.props, 'leftContent', false);
		return (
			<div className={leftContent ? classes.rootRight : classes.root}>
				<span />
				<FiberManualRecordIcon
					className={clsx(classes.dot, {
						[classes.dotLeft]: !leftContent,
						[classes.dotRight]: leftContent
					})}
				/>
				<div
					className={clsx(classes.outline, {
						[classes.borderRight]: leftContent,
						[classes.outlineLeft]: leftContent,
						[classes.outlineRight]: !leftContent
					})}
				/>
				<div className={clsx(classes.mainContainer, { [classes.flexEnd]: leftContent })}>
					<div className={classes.imageContainer}>
						<img src={image} alt="name" className={classes.image} />
					</div>
					{contentUp !== '' && (
						<div
							className={clsx(classes.contentUp, classes.infoBlock, {
								[classes.right]: !leftContent,
								[classes.left]: leftContent
							})}
						>
							{ProgrammesContent.filter(
								(item) => item.title === contentUp
							).map((item, i) => this.getContent(item))}
						</div>
					)}
					{contentDown !== '' && (
						<div
							className={clsx(classes.contentDown, classes.infoBlock, {
								[classes.right]: !leftContent,
								[classes.left]: leftContent
							})}
						>
							{ProgrammesContent.filter(
								(item) => item.title === contentDown
							).map((item, i) => this.getContent(item))}
						</div>
					)}
				</div>
			</div>
		);
	}
}

export default withStyles(Style, { name: 'DmartCorpFreshImageInfoBlocks' })(ImageInfoBlocks);
